import React from 'react';
import logo from './image.jpg';
import './App.css';

class App extends React.Component {
    constructor(props) {
      super(props);
    this.state = {
      R1:.1,
      R2:1,
      R3:1,
      C:1,
      Av: 1,
      Fc:1,
      Q:1,
      };
  }
  
    handleChange(e) {
   this.setState({ [e.target.name] : e.target.value });
    let {R1,R2,R3,Fc,Av,C,Q}=this.state;
    let av=R2/(2*R1);
    let fc=1/(2*3.14*C*Math.sqrt(R1*3.14*R3)*R2);
    let a1=R2/(R1*R3*3.14);
    let q= (1/2)*Math.sqrt(a1);
    this.setState({
      Av:av.toFixed(1),
      Fc:fc.toFixed(1),
      Q:q.toFixed(1)
    });
  }
  
  render(){

    let {R1,R2,R3,Fc,Av, C,Q}=this.state;
    return (
      <div className="App">
      <h2>BPF circuit Calculator by Jean Catulle</h2>
      <h3>A calculator that calculates values in the BPF circuit to meet goal criteria (Q, AV and f0)!!</h3>
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <div className='coll'>
            <h2>Q: {Q}</h2>
            <h2>Fo: {Fc}Hz</h2>
            <h2>Av: {Av}v</h2>
          </div>
           <div className='coll'>
            <label for="">R1?</label>
            <input 
              type="R1" 
              name="R1" 
              placeholder='R1'
              // value={this.state.R1}
              onChange={this.handleChange.bind(this)}
              />            
            <label for="">R2?</label>
            <input 
              type="R2" 
              name="R2" 
              placeholder='R2'
              // value={this.state.R2}
              onChange={this.handleChange.bind(this)}
              />            
            <label for="">R3?</label>
            <input 
              type="R3" 
              name="R3" 
              placeholder='R3'
              // value={this.state.R3}
              onChange={this.handleChange.bind(this)}
              />            
            <label for="">C?</label>
            <input 
              type="C" 
              name="C" 
              placeholder='C'
              // value={this.state.C}
              onChange={this.handleChange.bind(this)}
              />
           </div>
        </header>
      </div>
    );
  }
}

export default App;
